<template>
    <div>
        <div class="banner">
            <div class="help_main">
                <p class="welcome">UNIONE</p>
                <div class="search">
                    <el-autocomplete class="inline-input" v-model="state1" :fetch-suggestions="querySearch" :trigger-on-focus="false" placeholder="~" @select="handleSelect" ></el-autocomplete>
                    <el-button></el-button>
                </div>
            </div>
        </div>
                    
        <!--  -->
        <div class="problem">
            <h5></h5>
            <div class="message">
                <ul>
                    <li>UNIONE</li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                </ul>

                <ul>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li>&</li>
                    <li>Ripple (XRP)</li>
                </ul>

                <ul>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                </ul>
            </div>  
        </div>
    </div>
</template>
<script>
export default {
   name:'Wiki',
   data() {
      return {
        restaurants: [],
        state1: ''
      };
    },
    methods: {
      querySearch(queryString, cb) {
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        //  callback 
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      loadAll() {
        return [
          { "value": "" },
          { "value": "" },
          { "value": ""},
        ];
      },
      handleSelect(item) {
        console.log(item);
      }
    },
    mounted() {
      this.restaurants = this.loadAll();
    }
}
</script>


<style>
*{margin: 0;padding: 0;}
ul li{list-style: none;}
.banner{width: 100%;height: 378px;background: url('~@/assets/img/help_01.png') no-repeat;}
.help_main{width: 1120px;margin: 0 auto;color: #eaf3ff;font-size: 28px;text-align: center;overflow: hidden;}
.welcome{margin: 126px 0 60px 0;}
.search{width: 635px;height: 55px;margin: 0 auto;}
/*  */
.problem{width: 1120px;min-height:400px;margin: 0 auto;overflow: hidden;}
.problem h5{font-size: 20px;color: #8d9fb8;margin-top: 57px;background: url("~@/assets/img/problem.png") 7% no-repeat;text-indent: 110px;}
.message ul{float:left;font-size: 16px;color: #a2b2c8;line-height: 50px;margin-top: 40px;}
.message ul:first-child{width: 340px;margin-left: 108px;}
.message ul:nth-child(2){width: 439px;}
</style>






